import React from 'react';
import styled from 'styled-components';
import VideoImg from '../images/video.png'



const About = () => {
  return (
    <AboutBackgorund>
      <AboutWrapper >
        <LeftContentWrapper>
          <h1>Whats Craze all About?</h1>
          <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Suspendisse fringilla fringilla nisl congue congue. Maecenas nec condimentum libero, at elementum mauris. </p>
          <p>Phasellus eget nisi dapibus, ultricies nisl at, hendrerit risusuis ornare luctus id sollicitudin ante lobortis at.</p>
          <p>Lorem ipsum dolor sit amet</p>
          <p>Lorem ipsum dolor sit amet consectetur elt</p>
        </LeftContentWrapper>
        <Video src={VideoImg} alt="" />
      </AboutWrapper>
    </AboutBackgorund>
  );
};

const AboutBackgorund = styled.div`
  background-color: #e7e7e7;
  padding: 4em;
`;

const AboutWrapper = styled.div`
  margin: auto;
  display: flex;
  color: white;
  text-align: left;
  max-width: 60em;
  flex-direction: row;
  align-content: space-between;
  @media (max-width: 980px ){
    flex-direction: column;
  }
`;

const LeftContentWrapper = styled.div`
  h1 {
    text-align: left;
  }
  p {
    text-align: left;
    color: 848484;
  }
  @media (max-width: 980px ){
    h1 {
      text-align: center;
    }
  }
`;

const Video = styled.img`
  width: 100%;
  height: 100%;
  margin-left: 2em;
  @media (max-width: 980px ){
    margin: 0em;
    margin-top: 2em;
  }
`;

export default About;


